<template>
  <div class="center">
    <div class="grey_bg">
      <div class="content">
        <div class="content-header">
          <div class="container">
            <h1 class="content-heading"></h1>
          </div>
        </div>
        <!--  -->
        <div class="container">
          <section class="list">
            <div class="card">
              <div class="card-main">
                <div class="myAccount">
                  <div class="account">
                    <el-button type="primary" @click="showUserInfo"
                      ></el-button
                    >
                    <Detail />
                  </div>
                </div>
                <el-row type="flex">
                  <el-col>
                    <el-row>
                      <el-col>
                        <ul class="card-inner card-mine card-beautify">
                          <li> :</li>
                          <li>{{ name }}</li>
                          <el-divider></el-divider>
                          <li> :</li>
                          <li>{{ e_mail }}</li>
                          <el-divider></el-divider>
                        </ul>
                      </el-col>
                    </el-row>
                  </el-col>
                  <el-col>
                    <!-- css -->
                    <CatDance />
                  </el-col>
                  <el-col>
                    <el-row>
                      <el-col>
                        <ul
                          class="card-inner card-mine card-player card-beautify"
                        >
                          <li> :</li>
                          <li>
                            {{ os }}
                            <span class="svg-container">
                              <svg-icon :icon-class="os" />
                            </span>
                          </li>
                          <el-divider></el-divider>
                          <CPlayer :playlist="playlist" />
                          <el-divider></el-divider>
                        </ul>
                      </el-col>
                    </el-row>
                  </el-col>
                </el-row>
              </div>
            </div>
          </section>
          <section class="list">
            <div class="card">
              <div class="card-main">
                <div style="margin-bottom: 20px;">
                  <el-button
                    ref="BatchDelete"
                    type="primary"
                    @click="BatchDelete"
                    ></el-button
                  >
                </div>
                <!--  -->
                <DevicesTable
                  :handleDelete="handleDelete"
                  :tableData2="tableData2"
                  :handleChange="handleChange"
                />
              </div>
            </div>
            <!--  -->
            <Paginations
              :total="total"
              :size="size"
              :page="page"
              v-on:sizeValue="sizeValue"
              v-on:pageValue="pageValue"
            />
          </section>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CPlayer from '@/components/c-player'
import CatDance from './components/chain'
import serviceDialog from '@/components/serviceDialog'
import DevicesTable from './components/table'
import Detail from './components/detail'
import Paginations from './components/pagination'
import { CodeToText } from 'element-china-area-data'
import { loadFromLocal } from '@/common/local-storage'
import { Msg, ComfirmMsg } from '@/utils/message'
import { mapState, mapMutations, mapActions, mapGetters } from 'vuex'
export default {
  name: 'center',
  components: {
    Detail,
    DevicesTable,
    Paginations,
    CatDance,
    CPlayer,
    serviceDialog,
  },
  data() {
    return {
      show: false,
      playlist: [],
      id: '',
      username: '',
      length: '',
      tableData2: [],
      total: '',
    }
  },
  methods: {
    ...mapMutations(['SET_SIZES', 'SET_PAGES', 'SET_DRAWER']),
    ...mapActions([
      'getInfo',
      'getDevieces',
      'deleteDevices',
      'BatchDeleteDevices',
    ]),
    userInfo() {
      this.getInfo({ params: { username: this.name } })
    },
    showUserInfo() {
      this.SET_DRAWER(true)
      this.userInfo()
    },
    handleChange(val) {
      this.multipleSelection = val
    },
    pageValue(pageValue) {
      this.SET_PAGES(pageValue)
      this.query()
    },
    sizeValue(sizeValue) {
      this.SET_SIZES(sizeValue)
      this.query()
    },
    /**
     * 
     * @class delete
     * @param {object}  val 
     * @param {number} index id
     */
    handleDelete(val, index) {
      ComfirmMsg(' ?', 'warning')
        .then(() => {
          this.deleteDevices({ _id: val._id, index }).then(() => {
            Msg('', 'success')
            this.query()
          })
        })
        .catch(() => {
          Msg('', 'info')
        })
    },
    /**
     * 
     * @class BatchDelete
     */
    BatchDelete() {
      if (this.multipleSelection) {
        ComfirmMsg(' ?', 'warning')
          .then(() => {
            const data = []
            this.multipleSelection.map((item) => {
              Object.getOwnPropertyNames(item).forEach(function (key) {
                if (key == '_id') {
                  data.push(item[key])
                }
              })
            })
            const param = { _id: JSON.stringify(data) }
            this.BatchDeleteDevices(param)
              .then(() => {
                this.query()
                Msg('', 'success')
              })
              .catch(() => {
                Msg('', 'error')
              })
          })
          .catch(() => {
            Msg('', 'info')
          })
      } else {
        Msg('', 'warning')
      }
    },
    /**
     * 
     * @class query
     */
    query() {
      this.getDevieces(this.name).then(() => {
        this.tableData2 = this.devices
        this.total = this.totals
      })
    },
  },
  computed: {
    ...mapGetters(['name', 'e_mail']),
    ...mapState({
      devices: (state) => state.homepage.devices,
      music: (state) => state.homepage.music,
      pages: (state) => state.homepage.pages,
      sizes: (state) => state.homepage.sizes,
      totals: (state) => state.homepage.totals,
    }),
  },
  created() {
    this.os = loadFromLocal('device')
    this.playlist = this.music
    this.page = this.pages
    this.size = this.sizes
  },
  mounted() {
    this.query()
  },
}
</script>
<style scoped>
.card-beautify {
  background-image: url('/static/image/beautify/comment-bg.png');
  background-repeat: no-repeat;
  background-position: 160px 100px;
  padding-left: 20px;
}
.card-player {
  padding: 0;
  background-position: 160px 0px;
}
.playerList {
  float: right;
}
</style>



